import React, { useState, useEffect } from 'react'
import firebase from 'firebase'
import { Link } from 'react-router-dom'
import { db } from '../Firebase'
import { userPosts, userData } from '../Redux/Actions/appActions'
import { ReactComponent as Add } from '../Icons/Add.svg'
import { connect } from 'react-redux'
import {
    Container,
    ProfileImage,
    FooterContainer,
} from './Footer.styles'
import { Text } from '../Styles/GlobalStyles.styles'

const Footer = (props) => {

    const [imageURL, setImageURL] = useState('')

    const getProfileImage = () => {
        firebase.auth().onAuthStateChanged((user)=> {
            if(user) {
                db.collection('users')
                .doc(user.uid)
                .get()
                .then(doc=> {
                    const data = doc.data()
                    if(data.profileImage) {
                        setImageURL(data.profileImage)
                    } else{
                        setImageURL("data:image/svg+xml;base64,")
                    }
                })
            }
        })
    }

    const clearProfileData = () => {
        props.dispatch(userData([]))
        props.dispatch(userPosts([]))
    }

    useEffect(()=> {
        getProfileImage()
    }, [])



    return(
        <FooterContainer visibility={props.profileLoaded ? 'visible' : 'hidden'}>
            {props.location.pathname.includes('/upload') ? 
            null
            :
            props.user ? 
            <Container>
                <Link to='/photo-app/upload'>
                    <Add style={{cursor: 'pointer'}} />
                </Link>
                <Link to='/photo-app/profile' onClick={clearProfileData}>
                    <ProfileImage src={imageURL} alt='profile'></ProfileImage>
                </Link>
            </Container>
            :
            <Container>
                <Link to='/photo-app/signup' style={{textDecoration: 'none'}}>
                    <Text size='24px'>Sign up</Text>
                </Link>
                <Link to='/photo-app/login' style={{textDecoration: 'none'}}>
                    <Text size='24px'>Log in</Text>
                </Link>
            </Container>
            }
        </FooterContainer>
        )
}

const mapStateToProps = state => ({
    user: state.app.user,
    profileLoaded: state.app.profileLoaded,
})

export default connect(mapStateToProps)(Footer)
